import it from "./isType";

export default {
	/**
	 * 
	 * @param {String} str: 
	 * @returns {boolean}
	 */
	hasHalfKana(str) {
		if( !it.isString(str) ) {
			throw new TypeError("hasHalfKana: arguments[0] must be string.");
		}
		
		return /[--]/.test(str);
	},
	
	/**
	 * 
	 * @param {String} str: 
	 * @param {boolean} denyHalfKana: 
	 * @returns {boolean}
	 */
	hasHalfChar(str, denyHalfKana) {
		if( !it.isString(str) ) {
			throw new TypeError("hasHalfChar: arguments[0] must be string.");
		}
		
		if(!denyHalfKana && this.hasHalfKana(str)) {
			return true;
		}
		
		const len = str.length;
		const re = /[]/;
		
		for(let i = 0; i < len; i++) {
			const c = str.charAt(i);
			// 14+2(%u)
			if( escape(c).length < 6 || re.test(c) ) {
				return true;
			}
		}
		return false;
	},
	
	/**
	 * 
	 * @param {String} str: 
	 * @returns {boolean}
	 */
	hasFullChar(str) {
		if( !it.isString(str) ) {
			throw new TypeError("hasFullChar: arguments[0] must be string.");
		}
		
		const len = str.length;
		const re = /[]/;
		
		for(let i = 0; i < len; i++) {
			const c = str.charAt(i);
			// 14+2(%u)
			if( escape(c).length >= 6 && !re.test(c) && !this.hasHalfKana(c) ) {
				return true;
			}
		}
		return false;
	}
};
