const it = {
	/**
	 * Object
	 * nullArrayfalse
	 * @param {mixed} arg
	 * @returns {Boolean}
	 */
	isObject(arg) {
		return typeof arg === "object" && arg !== null && !Array.isArray(arg);
	},
	
	/**
	 * ArrayArray.isArray
	 * @param {mixed} arg
	 * @returns {Boolean}
	 */
	isArray(arg) {
		return Array.isArray(arg);
	},
	
	/**
	 * Boolean
	 * @param {mixed} arg
	 * @returns {Boolean}
	 */
	isBoolean(arg) {
		return typeof arg === "boolean";
	},
	
	/**
	 * String
	 * @param {mixed} arg
	 * @returns {Boolean}
	 */
	isString(arg) {
		return typeof arg === "string";
	},
	
	/**
	 * Number
	 * NaNfalse
	 * @param {mixed} arg
	 * @returns {Boolean}
	 */
	isNumber(arg) {
		return typeof arg === "number" && !isNaN(arg);
	},
	
	/**
	 * Integer
	 * @param {mixed} arg
	 * @returns {Boolean}
	 */
	isInteger(arg) {
		return it.isNumber(arg) && Math.round(arg) === arg;
	},
	
	/**
	 * 
	 * @param {mixed} arg
	 * @param {mixed} [flgStrict]: true"1s"false
	 * @returns {Boolean}
	 */
	isNumeric(arg, flgStrict) {
		const t = typeof arg;
		return (t === "number" || t === "string") && !isNaN( parseFloat(arg) - (flgStrict ? arg : 0) );
	},
	
	/**
	 * Function
	 * @param {mixed} arg
	 * @returns {Boolean}
	 */
	isFunction(arg) {
		return typeof arg === "function";
	},
	
	/**
	 * HTMLElement
	 * @param {mixed} arg
	 * @returns {Boolean}
	 */
	isHTMLElement(arg) {
		try {
			//Using W3 DOM2 (works for FF, Opera and Chrom)
			return arg instanceof HTMLElement;
		} catch(e) {
			//Browsers not supporting W3 DOM2 don't have HTMLElement and
			//an exception is thrown and we end up here. Testing some
			//properties that all elements have. (works on IE7)
			return (
				typeof arg === "object" &&
				arg.nodeType === 1 &&
				typeof arg.style === "object" &&
				typeof arg.ownerDocument === "object"
			);
		}
	}
};

export default it;
